@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.node {
  width: 25px;
  height: 25px;
  outline: 1px solid rgba(0, 190, 218, 0.75);
  display: inline-block;
  border-radius: 10%;
}

.wall {
  background-color: rgb(3, 3, 53);
}

.destination {
  background-color: green;
}

.source {
  background-color: red;
}

.vis {
  -webkit-animation-name: visitedAnimation;
          animation-name: visitedAnimation;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

.shortest {
  -webkit-animation-name: shortestpathAnimation;
          animation-name: shortestpathAnimation;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgb(243, 243, 188);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(17, 104, 217, 0.75);
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 190, 218, 0.75);
  }
}

@keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgb(243, 243, 188);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(17, 104, 217, 0.75);
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 190, 218, 0.75);
  }
}

@-webkit-keyframes shortestpathAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgb(243, 243, 188);
    border-radius: 100%;
  }

  50% {
    background-color: rgb(240, 240, 126);
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(246, 246, 51);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(255, 208, 0, 0.75);
  }
}

@keyframes shortestpathAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgb(243, 243, 188);
    border-radius: 100%;
  }

  50% {
    background-color: rgb(240, 240, 126);
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(246, 246, 51);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(255, 208, 0, 0.75);
  }
}

.grid {
  margin-top: 100px;
}

.space {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

*:focus {
  outline: none;
}

.nav {
  display: flex;
  align-items: center;
  font-family: "Anonymous Pro";
  font-size: 18px;
  padding: 5px;
  margin: 0;
  background-color: rgba(23, 63, 185, 0.87);
}

.navitems {
  margin-right: 30px;
  border-radius: 3%;
}

.btn {
  font-family: "Anonymous Pro";
  background-color: rgb(126, 223, 126);
  border-width: 0;
  height: 30px;
  font-size: 15px;
}

.btn:hover {
  background-color: rgb(107, 196, 107);
}

#homelink {
  font-size: 25px;
  font-weight: bolder;
  color: greenyellow;
}

.list {
  list-style-type: none;
}

#dropdown {
  float: left;
  overflow: hidden;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 110px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

#dropdown:hover .dropdown-content {
  display: flex;
  flex-flow: column;
}

.dropdownbtn {
  font-family: "Anonymous Pro";
  background-color: rgb(82, 134, 230);
  border-width: 0;
  color: white;
  font-size: 15px;
}

.dropdownbtn:hover {
  color: rgb(241, 245, 8);
}

.dropdown-content a {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
  font-size: 13px;
}

